import React, {useState} from 'react';
import TextField from '@mui/material/TextField';
import './Home.css';
import albert from './../albert-4.png';
import albertResult from './../albert.png';
import {
    doc,
    collection,
    runTransaction,
} from "firebase/firestore";
import {} from 'firebase/analytics'
import db from "./database/firebase";
import FormControl from "@mui/material/FormControl";
import {createTheme} from "@mui/material/styles";
import ThemeProvider from "@mui/material/styles/ThemeProvider";

function Home() {
    const [name, setName] = useState(''); // 
    const [phone, setPhone] = useState(''); //  
    const [commandName, setCommandName] = useState(''); //  
    const [commandNumber, setCommandNumber] = useState(0); //  
    const [loading, setLoading] = useState(false); //  
    const theme = createTheme({
        palette: {
            error: {
                main: "#302d30",
            }
        }
    });

    const addTeam = async () => {
        const totalCommandsNumberRef = doc(db, 'total_commands', 'number');
        const commandRef = collection(db, "commands");

        try {
            const newTeam = await runTransaction(db, async (transaction) => {
                setLoading(true);
                const totalCommandsDoc = await transaction.get(totalCommandsNumberRef);

                if (!totalCommandsDoc.exists()) {
                    throw "Document does not exists";
                }

                let commandNumber = totalCommandsDoc.data().commands_number + 1;

                transaction.set(doc(commandRef), {
                    name: name,
                    phone: phone,
                    command_name: commandName,
                    date: new Date(),
                    command_number: commandNumber,
                });

                transaction.update(totalCommandsNumberRef, {commands_number: commandNumber});

                setCommandNumber(commandNumber);
            }).then(() => {

            });
        } catch (e) {
            console.error(": \n", e);
        } finally {
            setLoading(false);
        }
    }

    //  
    const comeBack = () => {
        setCommandNumber(0);
        setName('');
        setPhone('');
        setCommandName('');
    }

    if (commandNumber === 0) {

        return <div className="home">
            <div className="card">
                <div className="image">
                    <img src={albert} alt="Logo"/>
                </div>
                <h1> </h1>
                <FormControl fullWidth sx={{m: 1}}>
                    <ThemeProvider theme={theme}>
                        <TextField
                            className="text-field"
                            id="outlined-adornment-amount"
                            label=""
                            size="small"
                            value={name}
                            onChange={(e) => setName(e.target.value.trim())}
                            required
                            error={!Boolean(name)}
                            helperText={!Boolean(name) ? " ''  " : ""}
                        />
                    </ThemeProvider>
                </FormControl>
                <FormControl fullWidth sx={{m: 1}}>
                    <ThemeProvider theme={theme}>
                        <TextField
                            className="text-field"
                            id="outlined-adornment-amount"
                            label=""
                            size="small"
                            required
                            type="number"
                            placeholder="87759844789"
                            value={phone}
                            onChange={(e) => setPhone(e.target.value.trim())}
                            error={!Boolean(phone)}
                            helperText={!Boolean(phone) ? " ''  " : ""}
                        />
                    </ThemeProvider>
                </FormControl>
                <FormControl fullWidth sx={{m: 1}}>
                    <ThemeProvider theme={theme}>
                        <TextField
                            className="text-field"
                            required
                            id="outlined-adornment-amount"
                            label=""
                            value={commandName}
                            size="small"
                            error={!Boolean(commandName)}
                            onChange={(e) => setCommandName(e.target.value.trim())}
                            helperText={!Boolean(commandName) ? " ''  " : ""}
                        />
                    </ThemeProvider>
                </FormControl>
                <button disabled={!Boolean(name) || !Boolean(phone) || !Boolean(commandName) || loading}
                        onClick={addTeam}>
                    {loading ? '...' : ""}
                </button>
                <sup>  ,      </sup>
            </div>
        </div>

    } else {

        return <div className="home">
            <div className="card">
                <div className="image">
                    <img src={albert} alt="Logo"/>
                </div>
                <div className="text-center">
                    <h1> : <span className="red-text">
                        {commandNumber}
                    </span></h1>
                    <h3 className="red-text">,   .</h3>
                    <img src={albertResult} alt="" className="albert-image"/>
                </div>
                <button onClick={comeBack}>
                     
                </button>
            </div>
        </div>

    }
}

export default Home;
